import { Component, ViewChild, Renderer2, ElementRef, Output, EventEmitter, Input } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { icons } from '../../../icons';

@Component({
  selector: 'detail-component',
  templateUrl: './detail-component.html',
  styleUrls: ['detail-component.scss']
})
export class DetailComponent {
  @Input() item;
  @Input() theme;
  @Output() closeEvent = new EventEmitter();
  @ViewChild('content') content: ElementRef; 
  @ViewChild('mainIcon') mainIcon: ElementRef; 
  current;
  currentTheme;
  icons=icons;

  colors1 = [
    "#fe3f65",
    "#ffb645",
    "#fff289",
    "#a9e270",
    "#79a14b"
  ]
  colors2 = [
    "#37b8dd",
    "#5459eb",
    "#cf67ff",
    "#9b9b9b",
    "#000000"
  ]

  themeName;
  iconName;
  themeStyle;
  styleList;

  constructor(
    public router: Router,
    public renderer: Renderer2
  ) {}

  ngOnInit() {
    let themeList = this.theme.split('-');
    
    this.themeName = themeList[1];
    this.themeStyle = themeList[2];
    this.styleList = Object.keys(icons[this.themeName]);
    this.styleList = this.styleList.map(ele => {
      let getIcon = this.icons[this.themeName][ele].filter(innerEle => innerEle.name == this.item.name)[0];
      if(getIcon){
        getIcon.style = ele;
        getIcon.active = ele == 'fill';
        ele == 'fill' ? this.currentTheme = getIcon : ""
        return getIcon;
      }else{
        return false;
      }
    }).filter(ele => ele)
  }

  ngAfterViewInit() {}

  changeColor(color, e){
    if(this.current){
      this.current.style.backgroundColor = "#FFFFFF";
    }
    this.mainIcon.nativeElement.style.color = color;
    e.target.style.backgroundColor = color;
    this.current = e.target;
  }

  themeChange(style){
    this.currentTheme.active = false;
    this.currentTheme = style;
    this.currentTheme.active = true;
  }

  dismiss(){
    Observable.of(1)
    .do(()=>{
      this.content.nativeElement.classList.remove('enter');
      this.content.nativeElement.classList.add('leave');
    })
    .delay(500)
    .subscribe(()=>{
      this.closeEvent.emit();
    })
  }

}
