import { Component, ViewChild, Renderer2, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';

@Component({
  selector: 'main-desk-page',
  templateUrl: './main-desk.html',
  styleUrls: ['main-desk.scss']
})
export class MainPageDesk {

  @ViewChild('arrowWrapper') arrowWrapper: ElementRef;

  constructor(
    public router: Router,
    public renterer: Renderer2
  ) {}

  ngOnInit() {}

  ngAfterViewInit() {}

  ngOnDestroy() {}

  start(e){
    Observable.of([''])
    .do(() => {
      this.arrowWrapper.nativeElement.classList.add('active')
    })
    .delay(2000)
    .subscribe(res => {
      this.router.navigate(['/list'])
    })
    
  }

}
