import { Component, ViewChild, Renderer2, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { icons } from '../../icons';
import { ThemeService } from '../../services/theme.service';

@Component({
  selector: 'list-page',
  templateUrl: './list.html',
  styleUrls: ['list.scss']
})
export class ListPage {
  @ViewChild('bigNumber') bigNumber: ElementRef;
  @ViewChild('weatherLogo') weatherLogo: ElementRef;
  @ViewChild('nameB') nameB: ElementRef;
  @ViewChild('nameSky') nameSky: ElementRef;
  @ViewChild('infoWrapper') infoWrapper: ElementRef;
  @ViewChild('arrow') arrow: ElementRef;
  @ViewChild('searchIcon') searchIcon: ElementRef;
  @ViewChild('searchInput') searchInput: ElementRef;

  contentState = false;
  menuState = false;
  guideState = false;
  detailState = false;
  searchInputState = false;

  detailActiveItem = ""
  detailActiveTheme = ""
  bigNumberText = ""
  activeIcon:any = {
    themeName: "",
    list: []
  };
  filteredList;

  currentTheme:any;
  searchText:string;

  constructor(
    public router: Router,
    public renderer: Renderer2,
    public themeService: ThemeService
  ) {}

  ngOnInit() {
    this.changeActiveIcon("weather", "fill");
    this.currentTheme = this.themeService.themeList[0];
    this.bigNumberText = "01";
  }

  changeActiveIcon(themeName, theme){
    this.activeIcon.themeName = `gonicon-${themeName}-${theme}`;
    this.activeIcon.list = icons[themeName][theme];
    this.filteredList = this.activeIcon.list;
  }

  ngAfterViewInit() {
    this.renderer.addClass(this.bigNumber.nativeElement, 'animated');
    this.renderer.addClass(this.weatherLogo.nativeElement, 'animated');
    this.renderer.addClass(this.nameB.nativeElement, 'animated');
    this.renderer.addClass(this.nameSky.nativeElement, 'animated');
    this.renderer.addClass(this.infoWrapper.nativeElement, 'animated');
  }

  menuOpen(e) {
    this.menuState = true;
    this.contentState = true;
  }

  menuClose(e) {
    this.currentTheme = e;
    this.bigNumberSet(this.currentTheme);
    this.changeActiveIcon(e.theme, "fill")
    this.menuState = false;
    this.contentState = false;
  }

  bigNumberSet(theme){
    let tempNumber:any = this.themeService.themeList.indexOf(theme) + 1;
    tempNumber = "0" + tempNumber;
    this.bigNumberText = tempNumber.substr(-2);
  }

  guideOpen(){
    if(this.arrow.nativeElement.classList.contains('active')) return false;

    Observable.of([''])
    .do(()=>{
      this.arrow.nativeElement.className += " active";
    })
    .delay(500)
    .subscribe(res => {
      this.arrow.nativeElement.className 
        = this.arrow.nativeElement.className.replace(/ active/g, "");
      this.guideState = true;
      this.contentState = true;
    })
  }

  guideClose(){
    this.guideState = false;
    this.contentState = false;
  }

  detailOpen(item){
    this.detailActiveItem = item;
    this.detailActiveTheme = this.activeIcon.themeName;

    this.detailState = true;
    this.contentState = true;
  }

  detailClose(){
    this.detailState = false;
    this.contentState = false;
  }

  search(e){
    this.filteredList = e.trim()
      ? this.activeIcon.list.filter(ele => ele.name.match(e.trim()))
      : this.activeIcon.list;
  }

  searchActive(e){
    if(this.searchIcon.nativeElement.classList.contains('active')) return false;

    Observable.of([''])
    .do(() => {
      this.searchIcon.nativeElement.classList.add('active');
      this.searchInput.nativeElement.classList.add('active');
    })
    .delay(500)
    .subscribe(res => {
      this.searchInputState = true;
    })
  }

  searchDeactive(){
    if(!this.searchIcon.nativeElement.classList.contains('active')) return false;

    Observable.of([''])
    .do(() => {
      this.searchIcon.nativeElement.classList.remove('active');
      this.searchInput.nativeElement.classList.remove('active');
      this.searchIcon.nativeElement.classList.add('deactive');
      this.searchInput.nativeElement.classList.add('deactive');
      this.searchInputState = false;
    })
    .delay(500)
    .subscribe(res => {
      this.searchIcon.nativeElement.classList.remove('deactive');
      this.searchInput.nativeElement.classList.remove('deactive');
    })
  }

  contentClick(e){
    if(this.searchInput.nativeElement != e.target && this.searchInputState){
        console.log("test")
        this.searchDeactive()
      }
  }

}
