import { Component, ViewChild, Renderer2, ElementRef, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { ThemeService } from '../../../services/theme.service';


@Component({
  selector: 'list-menu-component',
  templateUrl: './list-menu-component.html',
  styleUrls: ['list-menu-component.scss']
})
export class ListMenuComponent {
  @Output() closeEvent = new EventEmitter();
  @ViewChild('content') content: ElementRef; 
  @ViewChild('description') description: ElementRef; 

  current = this.themeService.themeList[0];
  currentIndex;
  first = false;

  constructor(
    public router: Router,
    public renderer: Renderer2,
    public themeService: ThemeService
  ) {}

  ngOnInit() {
    
  }

  ngAfterViewInit() {}

  dismiss(){
    Observable.of(1)
    .do(()=>{
      this.content.nativeElement.classList.remove('enter');
      this.content.nativeElement.classList.add('leave');
    })
    .delay(500)
    .subscribe(()=>{
      this.closeEvent.emit(this.current);
    })
    // this.closeEvent.emit();
  }

  getIndex(e){
    if(!this.first){
      this.first = true;
      this.currentIndex = e;
    }else if(this.currentIndex != e){
      let action = this.currentIndex < e ? "left" : "right";
      this.currentIndex = e;
      this.description.nativeElement.classList.remove('fadeIn');
      this.description.nativeElement.classList.remove('animated');
      this.description.nativeElement.classList.remove('delay-sm');
      this.currentChange(this.themeService.themeList[e], action)
    }
  }

  currentChange(data, action){
    Observable.of(1)
    .do(()=>{
      this.description.nativeElement
      .classList.add('hide-'+action)
    })
    .delay(100)
    .do(() => {
      this.description.nativeElement
      .classList.remove('hide-'+action);
      this.current = data;
    })
    .do(() => {
      this.description.nativeElement
      .classList.add('enter-'+action)
    })
    .delay(100)
    .subscribe(() => {
      this.description.nativeElement
      .classList.remove('enter-'+action)
    })
    
  }

}
